<template>
	<div id="takeout">
		<public-header title="订单列表">
			<div slot="right" class="search-btn" @click="onSetSearch()">
				<span class="icon icon-search"></span>
			</div>
		</public-header>
		<public-footer :menufooter="menufooter" :showPreLoading="showPreLoading"></public-footer>
		<div class="content">
			<div class="tab-group flex-lr border-1px-b">
				<div class="tab-item" :class="{active: filter.items.status == 1}" @click='onChangeStatus(1)'>待接单</div>
				<div class="tab-item" :class="{active: filter.items.status == 2}" @click='onChangeStatus(2)'>已确认</div>
				<div class="tab-item" :class="{active: filter.items.status == 3}" @click='onChangeStatus(3)'>待配送</div>
				<div class="tab-item" :class="{active: filter.items.status == 4}" @click='onChangeStatus(4)'>配送中</div>
				<div class="tab-item" :class="{active: filter.items.status == 6}" @click='onChangeStatus(6)'>退款单</div>
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh">
				<van-list
					v-model="orders.loading"
					:finished="orders.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!orders.empty"
					class="order-list"
				>
					<template v-for="(item, index) in orders.data">
						<order-item
							:order="item"
							:order_index='index'
							@onChangeOrderStatus='onChangeOrderStatus'
						></order-item>
					</template>
				</van-list>
				<div class="no-data" v-else>
					<img src="static/img/order_no_con.png" alt="">
					<p>暂无订单!</p>
				</div>
			</van-pull-refresh>
		</div>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import cloudApp from '@/config/cloudapp.js'
	import publicHeader from "@/components/header"
	import publicFooter from "@/components/footer"
	import orderItem from "./components/orderItem"
	import {getOrderList, changeOrderStatus} from "./controller/order.js"
	export default {
		components: {
			publicHeader,
			publicFooter,
			orderItem
		},
		data() {
			return {
				showPreLoading: true,
				isRefresh: false,
				filter: {
					items: {
						status: 1,
						refund_status: 0
					}
				},
				orders: {
					page: 1,
					psize: 15,
					finished: false,
					loading: false,
					data: []
				},
				menufooter: {}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),

			onChangeStatus(status) {
				var force = false;
				if(status != this.filter.items.status) {
					force = true;
				}
				this.filter.items.status = status;
				this.filter.items.refund_status = 0;
				if(status == 6) {
					this.filter.items.refund_status = 1;
				}
				this.onLoad(force);
			},
			onLoad(force) {
				let that = this;
				getOrderList({
					vue: that,
					force: force,
					data: {
						filter: that.filter ? that.filter.items : {},
						menufooter: 1
					}
				});
			},

			onPullDownRefresh() {
				this.onLoad(true);
			},

			onChangeOrderStatus(options) {
				options.vue = this;
				changeOrderStatus(options);
			},

			onSetSearch() {
				this.setSearch({
						title: '订单搜索',
						type: 'filter',
						key: 'takeout',
						url: 'plateform/order/takeout/search',
						redirctUrl: 'pages/order/searchResult'
					}
				);
				this.$router.push(this.util.getUrl({path: '/pages/common/search'}));
			},
		},

		mounted() {
			var that = this;
			that.onLoad(false);
			that.util.imap();
			that.util.icloudapi();
		},

		created() {
			this.query = this.$route.query;
			if(this.query && this.query.status) {
				this.filter.items.status = this.query.status;
			}
		}
	}
</script>

<style>
	#takeout .content{
		padding-top: 45px;
	}
	#takeout .search-btn{
		height: 45px;
	}
	#takeout .search-btn .icon-search{
		font-size: 20px;
	}
	#takeout .tab-group{
		width: 100%;
		height: 45px;
		line-height: 45px;
		background-color: #fff;
		color: #333;
		position: fixed;
		top: 0px;
		z-index: 10;
		left: 0;
		right: 0;
	}
	#takeout #public-header~#public-footer~.content .tab-group{
		top: 46px;
	}
	#takeout .tab-group .tab-item{
		width: 40%;
		height: 100%;
		text-align: center;
		font-size: 14px;
		position: relative;
	}
	#takeout .tab-group .tab-item.active{
		color: #4FAE52;
	}
	#takeout .tab-group .tab-item.active:after{
		content: '';
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 0;
		border-bottom: 2px solid #4FAE52;
	}
	#takeout .van-pull-refresh{
		overflow: visible;
	}
	
</style>


